/* CharityClub Nonprofit HTML5 Template */

@media only screen and (max-width: 1250px) {
  .two-column-fluid {
    .column {
      width: 100%;
      min-height: 0px;
    }
    .left-column .content-box, .right-column .content-box {
      float: none;
      max-width: none;
      width: 100%;
      padding: 50px 20px;
    }
  }


  .two-col-bg .content-box {
    float: none;
    max-width: none;
    width: 100%;
    padding: 40px 20px;
  }
}

@media only screen and (max-width: 1199px) {
  .main-footer .footer-upper {
    padding: 70px 0 40px;
  }
}

@media only screen and (max-width: 1140px) {
  .main-menu .navigation > li {
    margin-right: 10px;
  }
}

@media only screen and (max-width: 1023px) {
  .header-style-two .lower-part {
    height: auto;
  }
  .main-header .header-upper .nav-outer {
    width: 100%;
  }
  .main-menu .navigation > li {
    margin-right: 10px;
    margin-left: 0px;
  }
  .header-style-two {
    .main-menu .navigation > li {
      margin-right: 10px;
      margin-left: 0px;
    }
    .lower-part .outer-box {
      padding-left: 15px;
    }
  }
  .featured-fluid-section {
    .text-column, .image-column {
      position: relative;
      width: 100%;
    }
    .dark-column .content-box {
      float: none;
      max-width: none;
      width: 100%;
      padding: 50px 30px 50px 15px;
    }
    .image-column {
      position: relative;
      min-height: 300px;
    }
  }
  .header-style-two {
    .header-top .info-box {
      display: inline-block;
      float: none;
      text-align: center;
      padding-left: 0px;
      margin: 0px 15px 10px;
      .icon {
        position: relative;
        left: 0px;
        top: 0px;
        margin-top: 0px;
        margin-bottom: 10px;
        width: 100%;
      }
    }
    .social-links {
      top: -10px;
    }
    .header-top .info-outer {
      width: 100%;
      text-align: center;
    }
  }
  .main-header {
    .header-lower .outer-box {
      padding-right: 0px;
    }
    .top-bar .info-outer {
      position: relative;
      border-radius: 0px;
    }
    .header-upper .logo {
      width: 100%;
      text-align: center;
      margin-bottom: 10px;
    }
  }
  .header-style-two .header-top .logo {
    width: 100%;
    text-align: center;
    margin-bottom: 10px;
  }
  .default-featured-column.style-two .content-box .theme-btn.btn-sm {
    padding: 2px 7px;
  }
}

@media only screen and (max-width: 991px) {
  .two-column-fluid .icon-box .icon {
    padding-left: 18px;
  }
  .default-featured-column {
    &.style-two .content-box .theme-btn.btn-sm {
      padding: 2px 9px;
    }
    .image-box .donate-piechart {
      bottom: -20px;
    }
  }
}

@media only screen and (min-width: 768px) {
  .main-menu .navigation > li > ul {
    display: block !important;
    visibility: hidden;
    opacity: 0;
    > li > ul {
      display: block !important;
      visibility: hidden;
      opacity: 0;
    }
  }
  .modal-dialog {
    width: 100%;
    max-width: 960px;
    margin: 50px auto;
  }
}

@media only screen and (max-width: 767px) {
  .xs-text-center {
    text-align: center;
  }
  .main-header .header-upper, .fixed-header.main-header .header-upper {
    position: relative !important;
  }
  .main-header .header-upper {
    .logo {
      width: 100%;
      text-align: center;
    }
    .nav-outer {
      width: 100%;
      padding: 0px;
    }
  }
  .header-style-two .lower-part .outer-box {
    padding-right: 15px;
    padding-bottom: 0px;
    padding-top: 10px;
  }
  .main-header .header-upper {
    .nav-outer .btn-donate {
      right: auto;
      left: 0px;
      top: 0px;
    }
    .social-box {
      display: none;
    }
  }
  .header-lower .outer-box {
    padding-right: 0px;
  }
  .main-menu {
    float: none;
    padding-top: 0px;
    z-index: 5;
    padding-bottom: 0px;
  }
  .fixed-header .main-menu {
    padding-top: 0px;
  }
  .main-menu {
    .collapse {
      max-height: 300px;
      overflow: auto;
      float: none;
      width: 100%;
      padding: 0px 0px 25px;
      border: 1px solid #eb5310;
      margin: 0px;
      -ms-border-radius: 3px;
      -moz-border-radius: 3px;
      -webkit-border-radius: 3px;
      -o-border-radius: 3px;
      border-radius: 3px;
      &.in {
        padding: 0px;
        border: 1px solid #eb5310;
        margin: 0px 0px 25px;
        -ms-border-radius: 3px;
        -moz-border-radius: 3px;
        -webkit-border-radius: 3px;
        -o-border-radius: 3px;
        border-radius: 3px;
      }
    }
    .collapsing {
      padding: 0px;
      border: 1px solid #eb5310;
      margin: 0px 0px 25px;
      -ms-border-radius: 3px;
      -moz-border-radius: 3px;
      -webkit-border-radius: 3px;
      -o-border-radius: 3px;
      border-radius: 3px;
    }
    .navbar-header {
      position: relative;
      float: none;
      display: block;
      text-align: right;
      width: 100%;
      padding: 4px 0px 15px;
      right: 0px;
      z-index: 12;
    }
  }
  .header-style-two .main-menu .navbar-header {
    padding: 0px 0px 10px;
  }
  .main-menu .navbar-header .navbar-toggle {
    display: inline-block;
    z-index: 7;
    border: 1px solid #eb5310;
    background: #eb5310;
    float: none;
    margin: 0px;
    border-radius: 0px;
  }
  .header-style-two .main-menu .navbar-header .navbar-toggle {
    float: left;
    border-color: #ffffff;
  }
  .main-menu {
    .navbar-header .navbar-toggle .icon-bar {
      background: #ffffff;
    }
    .navbar-collapse > .navigation {
      float: none !important;
      margin: 0px !important;
      width: 100% !important;
      background: #eb5310;
      border: 1px solid #ffffff;
      > li {
        margin: 0px !important;
        float: none !important;
        width: 100%;
      }
    }
    .navigation > li > {
      a, ul:before {
        border: none;
      }
    }
    .navbar-collapse > .navigation > li > a {
      padding: 10px 10px !important;
    }
    .navigation > li {
      &.dropdown > {
        a::before, ul > li.dropdown > a::before, a::after, ul > li.dropdown > a::after {
          color: #fff;
          right: 15px;
        }
      }
      > ul > li {
        > {
          a:before, ul > li > a:before {
            color: #fff;
            right: 15px;
          }
        }
        &.dropdown > a:after {
          content: "\f0d7";
        }
      }
    }
    .navbar-collapse > .navigation {
      li.dropdown {
        .dropdown-btn {
          bottom: 0;
          left: 0;
          opacity: 0;
          position: absolute;
          right: 0;
          top: 0;
          width: 100%;
          z-index: 9999;
          display: block;
        }
        &:after {
          display: none !important;
        }
      }
      > li {
        > ul {
          position: relative;
          border: none;
          float: none;
          visibility: visible;
          opacity: 1;
          display: none;
          margin: 0px;
          left: auto !important;
          right: auto !important;
          top: auto !important;
          width: 100%;
          background: #eb5310;
          -webkit-border-radius: 0px;
          -ms-border-radius: 0px;
          -o-border-radius: 0px;
          -moz-border-radius: 0px;
          border-radius: 0px;
          transition: none !important;
          -webkit-transition: none !important;
          -ms-transition: none !important;
          -o-transition: none !important;
          -moz-transition: none !important;
          > li {
            > ul {
              position: relative;
              border: none;
              float: none;
              visibility: visible;
              opacity: 1;
              display: none;
              margin: 0px;
              left: auto !important;
              right: auto !important;
              top: auto !important;
              width: 100%;
              background: #eb5310;
              -webkit-border-radius: 0px;
              -ms-border-radius: 0px;
              -o-border-radius: 0px;
              -moz-border-radius: 0px;
              border-radius: 0px;
              transition: none !important;
              -webkit-transition: none !important;
              -ms-transition: none !important;
              -o-transition: none !important;
              -moz-transition: none !important;
              border-top: 1px solid rgba(255, 255, 255, 1) !important;
              > li {
                border-top: 1px solid rgba(255, 255, 255, 1) !important;
                opacity: 1 !important;
                top: 0px !important;
                left: 0px !important;
                visibility: visible !important;
              }
            }
            border-top: 1px solid rgba(255, 255, 255, 1) !important;
            opacity: 1 !important;
            top: 0px !important;
            left: 0px !important;
            visibility: visible !important;
          }
          border-top: 1px solid rgba(255, 255, 255, 1) !important;
        }
        border-top: 1px solid rgba(255, 255, 255, 1) !important;
        opacity: 1 !important;
        top: 0px !important;
        left: 0px !important;
        visibility: visible !important;
        &:first-child {
          border: none;
        }
        > {
          a {
            padding: 10px 10px !important;
            line-height: 22px;
            color: #ffffff;
            background: #eb5310;
            text-align: left;
          }
          ul > li > {
            a, ul > li > a {
              padding: 10px 10px !important;
              line-height: 22px;
              color: #ffffff;
              background: #eb5310;
              text-align: left;
            }
          }
          a {
            &:hover, &:active, &:focus {
              background: #eb5310;
            }
          }
        }
        &:hover > a {
          background: #eb5310;
          color: #ffffff !important;
        }
        > ul > li {
          &:hover > a, > ul > li:hover > a {
            background: #eb5310;
            color: #ffffff !important;
          }
        }
        &.current > a, &.current-menu-item > a {
          background: #eb5310;
          color: #ffffff !important;
        }
      }
    }
    .navigation > li > ul:before {
      display: none !important;
    }
  }
  .main-header .header-top .top-right .social-links-one {
    display: none;
  }
  .revolution-slider .tp-caption {
    display: none !important;
  }
  .subscribe-intro .text-right {
    text-align: left !important;
  }
  .sec-title .pull-right {
    float: left !important;
  }
  .default-text-column h2, .sec-title h2, .two-column-fluid .content-box h2, .default-text-column h2 .normal-font {
    font-size: 22px !important;
  }
  .page-title h1 {
    font-size: 32px !important;
  }
  .contact-form-container {
    padding: 40px 15px;
  }
  .causes-section.list-view {
    .image-box, .content-box {
      width: 100%;
    }
  }
  .sidebar-page .padd-right-20 {
    padding-right: 0px !important;
  }
  .gallery-section.full-width .image-box {
    width: 50%;
  }
  .donation-form-outer {
    padding: 30px 15px;
  }
  .list-view .default-featured-column .content-box {
    padding-top: 20px;
  }
  .error-section .error-text {
    margin-bottom: 40px;
  }
  .header-style-two .lower-part .outer-box .btn-box {
    height: 54px;
    padding: 10px;
    &:before {
      display: none;
    }
  }
}

@media only screen and (max-width: 615px) {
  .two-column-fluid.style-two .icon-box {
    .icon {
      font-size: 36px;
      margin-top: 12px;
      padding-left: 0;
    }
    .lower-box {
      border: none;
    }
    h4 {
      font-size: 20px;
    }
  }
  .sponsors-section .sponsors-slider li img {
    width: auto;
    max-width: 100%;
  }
}

@media only screen and (max-width: 599px) {
  .header-style-two .header-top .info-outer {
    display: none;
  }
  .sidebar-page {
    .comments-area {
      .comment, .reply-comment {
        padding-left: 0px;
        margin-left: 0px;
      }
    }
    .about-author .author-desc {
      padding-left: 0px;
      margin-left: 0px;
    }
    .comments-area .comment-box .author-thumb, .about-author .author-thumb {
      position: relative;
      left: 0px;
      margin-bottom: 20px;
    }
  }
  .two-column-fluid {
    .icon-box {
      margin-bottom: 30px;
      .lower-box {
        padding: 0px;
        border: none !important;
      }
    }
    .theme-btn {
      margin: 0px 10px 10px;
    }
  }
  .error-section .error-text {
    .bigger-font {
      font-size: 100px;
    }
    .medium-text, h3 {
      font-size: 22px;
    }
  }
  .featured-fluid-section .theme-btn {
    margin-bottom: 10px;
  }
  .parallax-section h2, .main-features .title-box h1, .page-title h1 {
    font-size: 30px;
  }
  .main-features .title-box h2 {
    font-size: 24px;
  }
  .header-style-two {
    .social-links {
      top: 10px;
    }
    .header-top .info-box {
      width: 100%;
      margin: 0px 0px 10px;
    }
  }
}

@media only screen and (max-width: 479px) {
  .two-column-fluid .right-column {
    .quote-icon {
      display: none;
    }
    .outer-box {
      padding-left: 0px;
    }
  }
  .vertical-links-outer.events-post .default {
    padding: 15px;
    .image-thumb {
      position: static;
      margin-bottom: 12px;
      width: auto;
    }
  }
}